.home {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--background-main);
  color: var(--text-main); 
  width: 100%;
  height: 100%;
}

.content {
  composes: homeText from "../common/common.module.css";
  display: flex;
  align-items: center;
}

.about{
  flex-grow: 1;
  margin-top: 20px;
}

.links {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.imgContainer{
  position: relative;
}
.img {
  position:relative;
  display:block;
  width: 480px;
}
.gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(to bottom, rgba(0, 0, 0,0) 0%, rgba(0, 0, 0,0) 60%, rgba(0, 0, 0,0.8) 100%); /* W3C */
}



@media screen and (max-width: 1200px) {
  .content { 
    flex-direction: column-reverse;
  }
}


@media screen and (max-width: 600px) {
  .content {
    font-size: 20px;
  }
  .img { 
    width: 70vw;
  }
}

@media screen and (max-width: 400px) {
  .content {
    font-size: 15px;
  }
}
